export default {
	data() {
		return {
			username: "",
			password: "",
			userBO: {},
			loginRes: {},
			infoRes: {},
			val:""
		}
	},
	onLoad() {

	},
	methods: {
		wxland(e) {
			console.log(e.target.dataset.type);
			this.val = e.target.dataset.type
			uni.login({
				provider: this.val,
				success: ((loginRes) => {
					console.log(loginRes.authResult);
					this.loginRes = loginRes
					// 获取用户信息
					uni.getUserInfo({
						provider: this.val,
						success: ((infoRes) => {
							console.log(infoRes.userInfo);
							this.infoRes = infoRes.userInfo
						})
					});
				})
			});
			if (this.val === "sinaweibo") {
				this.infoRes.openId = this.infoRes.id
			}
			uni.request({
				url: `${this.$url}/appUnionLogin/${this.val}?qq=122212489`,
				method: 'POST',
				data: {
					infoRes: {},
					face: this.infoRes.avatarUrl,
					nickname: this.infoRes.nickName,
					openIdOrUid: this.infoRes.openId
				},
				success: res => {
					console.log(res.data);
					if (res.data.status === 200) {
						uni.navigateBack(-1)
						this.$store.state.user = res.data.data
					} else {
						uni.showToast({
							title: "登陆失败"
						})
					}
				},
				fail: (e) => {
					console.log(e);
				},
				complete: () => {}
			});
		},
		toland() {
			console.log(this.username);
			console.log(this.password);
			uni.request({
				url: `${this.$url}/user/registOrLogin?qq=122212489`,
				method: 'POST',
				data: {
					userBO: this.userBO,
					password: this.password,
					username: this.username
				},
				success: res => {
					console.log(res.data);
					if (res.data.status === 500) {
						uni.showToast({
							title: res.data.msg
						});
					} else {
						this.$store.state.user = res.data.data
						uni.switchTab({
							url: "../mymessage/mymessage"
						})
					}
				},
				fail: (e) => {
					console.log(e);
				},
				complete: () => {}
			});
		}
	},
	watch: {}
}
